.aside-container {
  @extend .grid;

  &__aside, &__content {
    @extend .col; }

  &__aside {
    width: percentage(255 / 1650);
    background-color: $white;

    @include r(1749) {
      width: 20%; }

    @include xl {
      position: fixed;
      z-index: 10;
      top: $header-height;
      left: 0;
      bottom: 0;

      padding-left: 0;
      padding-right: 0;
      margin-bottom: 0;

      width: rem(300);
      transform: translate(calc(-100% - #{$container--lg-padding}), 0);

      @include ready {
        @include tr(transform .5s $easeInQuad); }

      &--open {
        transform: translate(0, 0); } }

    @include lg {
      top: $header-height-lg; } }

  &__content {
    width: percentage((1650 - 255) / 1650);
    @include r(1749) {
      width: 80%; }
    @include xl {
      width: 100%; } } }

.aside {
  @include r(1749) {
    display: flex;
    flex-direction: column; }
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    color: $white;
    background-color: $accent;
    padding: rem(15) rem(25); }

  &__accordion {
    @include r(1749) {
      overflow: auto;
      flex-grow: 1; } }

  &__close {
    position: relative;
    top: auto;
    right: auto;

    @include xlmin {
      display: none; }

    @include hover {
      color: $c-text; } } }
