.s-descr {
  padding-top: rem(110);
  padding-bottom: rem(90);
  @include lg {
    padding-top: rem(80);
    padding-bottom: rem(80); }
  @include md {
    padding-top: rem(60);
    padding-bottom: rem(60); }
  @include sm {
    padding-top: rem(30);
    padding-bottom: rem(30); } }

.insta-items {
  padding-left: rem(30);
  padding-right: rem(30);
  padding-bottom: rem(30);

  &__inner {
    display: flex;
    flex-wrap: wrap; }

  &__item {
    width: percentage(1 / 6);
    @include lg {
      width: 20%; }
    @include md {
      width: 25%; }
    @include sm {
      width: 50%; } } }

.insta-item {
  $this: &;

  display: block;

  padding-top: 15%;
  position: relative;
  color: $white;
  font-size: em(12);
  text-align: center;
  background-color: $light;
  @include cover;
  @include before {
    @include coverdiv;
    background-color: rgba($black, .7);
    @include hidden;
    @include tr(opacity .3s, visibility .3s); }

  &:hover {
    #{$this}__inner, &::before {
      @include visible; } }

  @include lg {
    padding-top: 20%; }
  @include md {
    padding-top: 25%; }
  @include sm {
    padding-top: 40%; }


  &__inner {
    display: block;
    @include vcenter;
    z-index: 1;
    @include hidden;
    @include tr(opacity .3s, visibility .3s); }

  &__icon {
    display: block;
    max-width: rem(45);
    font-size: 0;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: rem(15); }

  &__title {
    text-decoration: underline; } }


.s-two-colored {
  $this: &;

  position: relative;
  &__top {
    padding-top: rem(90);
    padding-bottom: rem(70);
    @include lg {
      padding-top: rem(50);
      padding-bottom: rem(50); } }
  &__bottom {
    padding-top: rem(115);
    padding-bottom: rem(140);

    background-color: $accent;
    color: $white;

    @include lg {
      padding-top: rem(90);
      padding-bottom: rem(90); }

    &--with-footer {
      overflow: hidden;
      padding-bottom: 0 !important; } }

  &__img {
    @include cover;
    background-color: $dark;

    position: absolute;
    z-index: 1;
    right: $container--lg-padding;
    bottom: 0;

    padding-top: 37%;

    width: percentage(650 / 1740);

    @include lg {
      right: 0; }

    @include md {
      display: none; } }

  &__bottom-footer {
    position: relative;
    margin-right: rem(-25);
    margin-top: rem(85);

    @include lg {
      margin-right: 0; }

    @include before {
      border-top: 1px solid #414eeb;
      position: absolute;
      z-index: 0;
      top: 0;
      left: -$container--lg-padding;
      width: calc(100vw + #{$container--lg-padding}); } }


  &--bottom-offset {
    margin-bottom: rem(30);
    #{$this}__img {
      &.lazy--loaded {
        bottom: rem(-30); } } } }

.s-text-word {
  padding-top: vw(340);
  padding-bottom: rem(90); }

.s-dude {
  @include cover;
  background-color: $dark;
  color: $white;

  padding-top: rem(110);
  padding-bottom: rem(110);
  margin-top: rem(190);

  position: relative;

  > .container {
    position: relative;
    z-index: 1; }

  @include md {
    margin-top: 0; }

  &__img {
    position: absolute;
    bottom: 0;
    left: 10%;
    z-index: 0;

    @include xl {
      left: 0; }

    @include md {
      display: none; } } }

.s-full {
  @include windowHeight($header-height);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  background-color: $dark;
  color: $white;

  @include lg {
    @include windowHeight($header-height-lg * 2); }

  &__icon {
    width: rem(54);
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: rem(20); }

  &__title {
    margin-bottom: rem(10); }

  &__subttl {
    margin-bottom: rem(15); } }

