.popup {
  $this: &;

  position: fixed;
  z-index: 20;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;

  overflow: auto;
  padding: rem(15);

  background-color: rgba($black, .7);

  @include vertical;
  @include hidden;
  @include ready {
    @include tr(opacity .3s, visibility .3s); }

  &__inner {
    display: inline-block;
    vertical-align: middle;
    font-size: 1rem;

    width: 100%;
    max-width: rem(615);

    background-color: $white;

    position: relative;

    padding: rem(50) rem(35) rem(65);

    @include lg {
      padding: rem(50) rem(20); } }

  &__title {
    @extend .block__title;
    margin-bottom: 0; }

  &__subttl {
    @extend .block__subttl;
    margin-bottom: rem(30); }

  &__form {
    text-align: left; }

  &--active {
    @include visible; }

  &--search {
    padding-top: 0;

    #{$this}__inner {
      vertical-align: top;
      max-width: rem(985);

      background-color: transparent;

      padding: 0;
      padding-right: rem(60); }

    #{$this}__close {
      color: #9a9a9a;
      font-size: rem(25);
      top: 50%;
      transform: translate(0, -50%); } }

  &--added-item {
    #{$this}__inner {
      padding: 0 !important;
      max-width: rem(1290); } } }

