html {
  font-size: $base-fz + px;
  line-height: 1.4;
  color: $c-text;
  @include reg;
  @include sm {
    font-size: $base-fz-sm + px; } }

body {
  background-color: $white;
  min-width: 320px;
  @include no-scroll; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  $this: &;
  &--loaded:not(#{$this}--no-fade-in) {
    animation: fadeIn 1s; } }

.logo {
  display: inline-block;
  font-size: 0; }

.main {
  background-color: $lightest;
  &--white {
    background-color: $white; } }

.container {
  $this: &;

  max-width: none;
  @include rmin(1625) {
    max-width: 1440px; }
  @include rmin(1400) {
    max-width: 1230px; }
  @include rmin(1200) {
    max-width: 1030px; }

  &--md {
    max-width: none;
    @include rmin(1800) {
      max-width: 1630px; } }

  &--lg {
    max-width: none !important;
    margin-left: 0;
    margin-right: 0;
    padding-left: $container--lg-padding;
    padding-right: $container--lg-padding;

    @include lg {
      padding-left: $container-padding;
      padding-right: $container-padding; } }

  &--lg-right-gap {
    @extend #{$this}--lg;
    padding-right: $container--lg-padding * 2;
    @include r(1749) {
      padding-right: $container--lg-padding + rem(15); }
    @include lg {
 } } }      // padding-right: $container-padding

.page-inner {
  max-width: 1650px; }

.word {
  text-transform: uppercase;
  font-weight: 800;
  font-size: em(254);
  line-height: .8;
  opacity: .1;
  white-space: nowrap;
  @include lg {
    font-size: em(220); }
  @include md {
    font-size: em(180); }
  @include sm {
    font-size: em(150); }
  @include xs {
    font-size: em(120); } }

.number-lg {
  color: $accent;
  font-weight: 800;
  font-size: em(260);
  line-height: .8;
  text-transform: uppercase;
  @include lg {
    font-size: em(220); }
  @include md {
    font-size: em(180); }
  @include sm {
    font-size: em(150); }
  @include xs {
    font-size: em(120); } }

.title-under {
  border-bottom: 1px solid $lighten;
  padding-bottom: rem(25);
  margin-bottom: rem(25);
  @include md {
    padding-bottom: rem(15);
    margin-bottom: rem(15); } }

.price {
  font-size: 1rem;
  line-height: .9;
  color: $c-text;
  strong {
    font-size: rem(32); }

  &--sm {
    strong {
      font-size: rem(21); } } }

.price-info {
  font-size: 12px;
  strong {
    font-size: 1rem; } }

.line-imgs {
  display: flex;
  flex-wrap: wrap;

  &__img {
    @include cover;
    background-color: $dark;

    width: percentage(1 / 6);
    padding-top: 17%; } }

@for $i from 1 through 10 {
  .col {
    &--#{$i}-10 {
      width: percentage($i/10); }
    &--xxs-#{$i}-10 {
      @include xxsmin {
        width: percentage($i/10); } }
    &--xs-#{$i}-10 {
      @include xsmin {
        width: percentage($i/10); } }
    &--sm-#{$i}-10 {
      @include smmin {
        width: percentage($i/10); } }
    &--md-#{$i}-10 {
      @include mdmin {
        width: percentage($i/10); } }
    &--lg-#{$i}-10 {
      @include lgmin {
        width: percentage($i/10); } }
    &--xl-#{$i}-10 {
      @include xlmin {
        width: percentage($i/10); } } } }

.img-item {
  @include cover;
  background-color: $light;
  padding-top: 100%;
  &.swiper-slide {
    padding-top: 18%;
    @include md {
      padding-top: 22%; }
    @include sm {
      padding-top: 30%; } } }

.logos {
  $logos-grid-gap: rem(15);

  display: flex;
  flex-wrap: wrap;

  margin-left: $logos-grid-gap / -2;
  margin-right: $logos-grid-gap / -2;

  &__item {
    width: percentage(1 / 6);
    padding-left: $logos-grid-gap / 2;
    padding-right: $logos-grid-gap / 2;
    margin-bottom: $logos-grid-gap;

    @include md {
      width: percentage(2 / 6); } } }

.logo-item {
  position: relative;
  padding-top: 60%;
  background-color: #f0f0f0;
  img {
    @include vcenter;
    max-height: 80%;
    max-width: 80%; } }

.link-blocks {
  display: flex;
  flex-wrap: wrap;

  &__item {
    width: rem(330);
    margin-right: rem(10);
    margin-bottom: rem(10); } }

.link-block {
  display: block;
  border: 1px solid  $c-border;
  border-radius: 3px;
  padding: rem(20);

  height: 100%;
  min-height: vw(185);

  &__title {
    display: block;
    font-weight: 700;
    font-size: em(21);
    margin-bottom: rem(50); }

  &__bottom {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end; }

  &__link-wrap {
    flex-grow: 1; }

  &__link {
    display: inline-flex;
    align-items: center;

    color: $c-link;
    @include after {
      @include icon-arrow(.5, .5, .1, currentColor, -135, em);
      display: block;
      margin-left: .5em; } }

  &--img {
    @include cover;
    background-color: $light;
    color: $white; } }

.close {
  @include icon-close(1.2em, 0.1em, currentColor);
  display: block;

  position: absolute;
  z-index: 1;
  top: rem(15);
  right: rem(15);

  @include tr(color .3s);

  @include hover {
    color: $accent; } }

.two-btns {
  display: flex;
  justify-content: center;

  @include md {
    display: block; }

  &__btn {
    margin-left: rem(15) / 2;
    margin-right: rem(15) / 2;
    @include md {
      margin-left: 0;
      margin-right: 0; }

    @include notlast {
      @include md {
        margin-bottom: rem(15); } } } }

.banner {
  @include cover;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  background-color: $dark;
  color: $white;

  padding: vw(180) rem(35) rem(35);
  height: 100%;

  @include lg {
    padding: vw(180) rem(20) rem(20); }

  &__title {
    font-size: em(32);
    font-weight: 700;
    margin-bottom: rem(10); }

  &__text {
    margin-bottom: rem(15); }

  &__content {
    max-width: rem(470); } }

