.gallery {
  &__slider {
    margin-bottom: rem(10);
    position: relative; }

  &__thumbs {
    height: vw(270);
    @include xxs {
      height: rem(120); } }

  &__img {
    @include cover;
    background-color: $dark;
    padding-top: percentage(450 / 735); }

  &__thumb {
    @include cover;
    background-color: $dark;
    height: vw(125);
    position: relative;

    @include xxs {
      height: rem(55); }

    @include before {
      @include coverdiv;
      background-color: rgba($white, .3); }

    &.swiper-slide-thumb-active {
      @include before {
        display: none; } } }

  &__label {
   @extend .product-card__label;
   top: auto;
   bottom: rem(30);
   background-color: $accent; } }

.video-gallery {
  display: flex;
  flex-wrap: wrap;

  margin-left: rem(-15);
  margin-right: rem(-15);

  &__item {
    // col col--lg-2-10 col--md-3 col--sm-4 col--xs-6
    margin-left: rem(15);
    margin-right: rem(15);
    margin-bottom: rem(30);
    width: calc(#{percentage(2 / 10)} - #{rem(30)});
    @include lg {
      width: calc(#{percentage(3 / 12)} - #{rem(30)}); }
    @include md {
      width: calc(#{percentage(4 / 12)} - #{rem(30)}); }
    @include sm {
      width: calc(#{percentage(6 / 12)} - #{rem(30)}); }
    @include xs {
      width: calc(100% - #{rem(30)}); } } }

.gallery-item {
  @include cover;
  background-color: $dark;

  display: block;
  // width: 100%
  position: relative;

  padding-top: 12%;
  @include lg {
    padding-top: 14%; }
  @include md {
    padding-top: 20%; }
  @include sm {
    padding-top: 30%; }
  @include xs {
    padding-top: 60%; } }

.thumbs-gallery {
  display: flex;
  flex-wrap: wrap;

  &__item {
    width: rem(100);
    margin-right: rem(10);
    margin-bottom: rem(10); } }

.thumb-item {
  $this: &;

  position: relative;
  color: $white;
  border: 1px solid  $c-border;


  width: rem(100);
  height: rem(70);

  @include xs {
    width: rem(80);
    height: rem(50); }

  img {
    @include vcenter;
    max-height: 100%; }

  @include before {
    @include coverdiv;
    z-index: 1;

    border: 2px solid  $accent;
    background-color: rgba($black, .35);

    @include tr(opacity .3s, visibility .3s);
    @include hidden; }

  @include hover {
    @include before {
      @include visible; }

    #{$this}__icon {
      @include visible; } }

  &__icon {
    @include vcenter;
    z-index: 2;
    font-size: em(25);

    @include hidden;
    @include tr(opacity .3s, visibility .3s); } }
