.search-results {
  max-width: rem(570);
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: rem(10);

    @include xs {
      display: block; } }

  &__top-btn {
    text-align: right;
    @include xs {
      text-align: inherit; } }

  &__meta-list {
    white-space: nowrap;
    @include xs {
      margin-bottom: rem(10); } }

  &__price-block {
    display: flex;
    justify-content: space-between;
    @include xs {
      display: block; } } }

.search-block {
  &__top {
    border: 1px solid  $c-border;
    background-color: $lighten;

    padding: rem(8) rem(20);

    color: $accent; }

  &__items {
    display: flex;
    flex-wrap: wrap;

    padding: rem(20) 0 0;

    margin-left: rem(-20);
    margin-right: rem(-20);

    @include sm {
      margin-left: rem(-10);
      margin-right: rem(-10); } }

  &__item {
    width: 25%;
    padding-left: rem(20);
    padding-right: rem(20);
    margin-bottom: rem(20);

    @include sm {
      padding-left: rem(10);
      padding-right: rem(10);
      margin-bottom: rem(10); } } }

.search-radio {
  $this: &;

  position: relative;
  display: block;
  font-size: em(12);
  text-align: center;

  &__input {
    position: absolute;
    @include hidden;

    &:checked + #{$this}__content {
      color: $accent;
      font-weight: 700;

      #{$this}__img {
        @include before {
          display: block; } } } }


  &__content {
    display: block; }

  &__img {
    display: block;
    position: relative;
    padding-top: 40%;
    background-color: $dark;

    width: rem(80);
    height: rem(60);

    margin-left: auto;
    margin-right: auto;

    @include notlast {
      margin-bottom: rem(15); }

    @include before {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      border: 2px solid  $accent;
      z-index: 1;
      display: none; }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover; } }

  &--sm {
    #{$this}__img {
      width: 100%;
      height: auto;
      padding-top: 65%; } }

  &--fit {
    #{$this}__img {
      width: 100%;
      height: auto;
      padding-top: 70%; } } }

