.out {
  $this: &;

  display: flex;
  flex-direction: column;
  @include windowMinHeight();

  padding-top: $header-height;

  @include lg {
    padding-top: $header-height-lg;
    padding-bottom: rem(50); }

  &__header {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;

    @include tr(transform .3s);
    transform: translate(0, 0);

    @include before {
      @include coverdiv;
      z-index: 9;

      background-color: $white;
      border: 1px solid  $c-border; }

    @include md {
      box-shadow: 0 0 10px rgba($black, .25);
      @include before {
        border: 0;
        border-bottom: 1px solid  $c-border; } } }

  &__main {
    flex-grow: 1; }

  &__side-controls {
    position: fixed;
    z-index: 15;
    top: 0;
    right: 0;
    bottom: 0;

    width: $container--lg-padding;

    @include lg {
      top: auto;
      width: 100%; } }

  &__aux-header {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;

    box-shadow: 0px 2px 4px 0px rgba(43, 37, 35, 0.35);
    background-color: $white;

    @include tr(transform .3s);


    transform: translate(0, -100%);

    @include md {
      display: none; } }

  &--double-header {
    #{$this}__aux-header {
      transform: translate(0, 0); }

    #{$this}__header {
      transform: translate(0, #{$header-height});

      @include lg {
        transform: translate(0, #{$header-height-lg}); }

      @include md {
        transform: translate(0, 0); } } } }

